<template>
<div>
  <div class="top-panel-main">
    <div class="back-but" @click="backLobby" v-if="showBackButton">
                   {{this.symbol}}
    </div>
    <div class="top-symb">
            
            <img src='../../assets/symbol6.png' width='30' height='30' class="imger">
            <div class="title-2">Rhyme Chat</div>
          
    </div>
    <button class="top-drop" @click="menuActive = !menuActive">/</button>
    
  </div>
  <div class="drop-menu" v-if="menuActive">
    <button class="menu-item" @click="navToRequests" id="top-menu-items">Requests</button>
    <hr>
    <p class="r_tag">{{nametag}}</p>
    <button class="menu-item" @click="navToHome" >Logout?</button>

  </div>
</div>
</template>

<script>
import firebase from 'firebase'
// import Backbutton from './Backbutton'
export default {
  name: 'Toppanel',
  props:[],
  components:{
    
  }
  ,
  data(){
    
    return{
      menuActive:false,
      email_tag:'',
      nametag:'',
      curr_route:'',
      showBackButton:false,
      symbol:'<'
      
    }
  },
  created(){
    this.$route.path === '/rhymeapplobby' ? this.showBackButton = false : this.showBackButton = true
    let user = firebase.auth().currentUser;
    let fs = firebase.firestore();
    fs.collection("users").doc(user.uid).onSnapshot((doc)=>{
       if(doc.exists){
         this.nametag=doc.data().r_nametag;
         
       }else{
         console.log('failed to parse')
       }
     })
    
    
     },
     

  methods:{
    
    navToHome(){
      firebase.auth().signOut().then(()=>{
        this.$router.replace({path:'/'});
      })
      
    },
    navToRequests(){
         this.$router.replace({path:'/rhymeapprequests'});
    },
     backLobby(){
            this.$router.replace({path:'/rhymeapplobby'})
        }
  }
}
</script>

<style scoped>
