<template>
   <div>
       <Toppanel :user_email="user_tag"/>
       
       <h1>Requests:</h1>
       <hr>
        <div class="request-main">

            <div class="request-item" v-bind:key="m.id" v-for="m in pending_reqs">
                <div class="img-container"></div>
                <div class="nametag">{{m.tag}}</div>
                <div class="okno-outer">
                        <div class="okno" @click="accept_request(m.id,m.tag)"><div></div></div>
                        <div class="okno" @click="reject_request(m.id)">&times;</div>
                </div>    
            </div>
    
        </div>   
    </div> 
</template>

<script>
import Toppanel from './Toppanel'

import firebase from 'firebase'
import {v4 as uuidv4} from 'uuid'
export default {
    name:'Requests',
    components:{
        Toppanel
    },
    data(){
        return{
            user_tag:'',
            pending_reqs:[]
        }
    },
   
     created(){
    let user = firebase.auth().currentUser;
    let fs = firebase.firestore();
    fs.collection("users").doc(user.uid).onSnapshot((doc)=>{
       if(doc.exists){
         this.user_tag=doc.data().r_nametag;
         this.pending_reqs=doc.data().r_pendingReqs;
         
       }else{
         console.log('failed to parse')
       }
     })
    this.user_email=user.email;
    
     },
     methods:{

         async accept_request(id,tag){
             let user = firebase.auth().currentUser;
             var arr = this.pending_reqs;
             var auths = [user.uid,id];
             var newFrndAdd = { fr_id:id,nametag:tag};
             var thisusernametag = {fr_id:user.uid,nametag:this.user_tag};
             var newConvoId = uuidv4();
             for( var i = 0; i < arr.length; i++){
                 if(arr[i].id === id){
                     arr.splice(i, 1); i--;
                     let user = firebase.auth().currentUser;
                     let fs = firebase.firestore();
                     let fs_user = fs.collection("users").doc(user.uid);
                     let fs_friend_user = fs.collection("users").doc(id);
                     let newConvo = fs.collection("convos").doc(newConvoId);
                            

                            await fs_user.set({
                                r_pendingReqs:arr
                                 },{merge:true}).catch((error)=>{
                                        console.log("Error getting documents: ", error);
                                    }); 

                            await fs_user.update({
                                    r_friends: firebase.firestore.FieldValue.arrayUnion(newFrndAdd),
                                    r_convoThreads: firebase.firestore.FieldValue.arrayUnion(newConvoId)
                                    }).catch((error)=>{
                                        console.log("Error getting documents: ", error);
                                    }); 
                            await fs_friend_user.update({
                                    r_friends: firebase.firestore.FieldValue.arrayUnion(thisusernametag),
                                    r_convoThreads: firebase.firestore.FieldValue.arrayUnion(newConvoId)
                                    }).catch((error)=>{
                                        console.log("Error getting documents: ", error);
                                    }); 
                        
                            await newConvo.set({
                                mainThread:[],
                                auths:auths
                            }).catch((error)=>{
                                        console.log("Error getting documents: ", error);
                                    });
                           
                           
                            
                                    break;
                 }
             }
              
         }, 

         async reject_request(id){
             var arr = this.pending_reqs;
             for( var i = 0; i < arr.length; i++){
                 if(arr[i].id === id){
                     arr.splice(i, 1); i--;
                     let user = firebase.auth().currentUser;
                     let fs = firebase.firestore();
                     let fs_user = fs.collection("users").doc(user.uid);
                     await fs_user.set({
                                r_pendingReqs:arr
                                 },{merge:true}).catch((error)=>{
                                        console.log("Error getting documents: ", error);
                                    }); 
                        break;

                 }

                }
             }
            }
}
</script>

<style scoped>
h1{
    margin-top:5rem ;
    margin-left:1rem;
    font-family: 'Poppins',sans-serif;
    color: white;
    text-shadow:2px 2px rgb(214, 101, 214);
}
hr{
    border: 1px solid rgb(113, 101, 141);
    
}
.request-main{
    /* background-color: burlywood; */
    margin-top: 1rem;
    width: 100%;
    /* background-color: blanchedalmond; */
    display: grid;
    grid-template-columns: auto auto auto;
    /* flex-direction: column; 
    align-items: center; */
    justify-items: center; 
    
}
@media only screen and (max-width: 768px) {
  .request-main{
    /* background-color: burlywood; */
    margin-top: 1rem;
    width: 100%;
    /* background-color: blanchedalmond; */
    display: grid;
    grid-template-columns: auto;

    /* flex-direction: column;*/
    justify-items: center; 
}

}

.request-item{
    width: 80%;
    height: 4rem;
    background-color: rgb(40, 27, 53);
    margin:0.3rem;
    border-radius: 0.3rem;
    padding:0.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-shadow: 0.5rem 0.5rem  rgb(56, 42, 94);
    border: 1px solid rgb(124, 82, 165);;
    
}
.request-item:hover{
    background-color: rgb(42, 29, 56);
}
.img-container{
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    background-color: rgb(64, 49, 80);
    
    background-size: cover;
}
.nametag{
    
    margin-left:1rem ;
    background-color: rgb(64, 49, 80);
    border-radius: 0.1rem;
    color: White;
    font-family: 'Poppins', sans-serif;
}
.okno-outer{
    margin-left: auto;
    display: flex;
    flex-direction:row ;
    
}
.okno{
    margin: 0.5rem;
    width:2rem;
    height:2rem;
    border:1px solid white;
    border-radius: 50%;
    text-align: center;
    color: rgb(241, 79, 79);
    font-weight: bold;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    cursor: pointer;
}
.okno:hover{
        background-color: #1f1f3a; 
}
.okno>div{
    width:0.5rem;
    height:0.5rem;
    border:2px solid rgb(51, 245, 180);
    border-radius: 50%;
     
}
</style>